import * as THREE from 'three'; //import
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { ColladaLoader } from 'three/examples/jsm/loaders/ColladaLoader.js';
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader.js';
import { MTLLoader } from 'three/examples/jsm/loaders/MTLLoader.js';
//import * as Stats from 'three-stats';
export function initThree(domEl) {
  const scene = new THREE.Scene(); //new
  scene.background = new THREE.Color('#04183C'); //bgc

  //daeLoader
  const loader = new ColladaLoader();
  loader.load(`${process.env.BASE_URL}idc/3d.dae`, LoadResult => {
    const idc = LoadResult.scene.children[0].clone();

    idc.rotation.x = -0.5 * Math.PI;
    idc.position.z = 350;
    let temp1, temp2;
    for (temp1 in idc.children) {
      //castrecieve
      idc.children[temp1].castShadow = true;
      idc.children[temp1].receiveShadow = true;
      for (temp2 in idc.children[temp1].children) {
        idc.children[temp1].children[temp2].castShadow = true;
        idc.children[temp1].children[temp2].receiveShadow = true;
      }
    }
    //console.log(idc)
    //const edges = new THREE.BoxHelper(  idc, 0x1535f7 );

    scene.add(idc);
  });
  const mtlLoader = new MTLLoader(); //
  const objLoader = new OBJLoader(); //OBJ
  mtlLoader.load(`${process.env.BASE_URL}model/tinyM.mtl`, materials => {
    // MaterialCreator
    console.log(materials);
    //objMaterialCreator
    objLoader.setMaterials(materials);
    objLoader.load(`${process.env.BASE_URL}model/tinyM.obj`, obj => {
      obj.name = 'test';
      console.log(obj);
      obj.scale.set(0.01, 0.01, 0.01); //obj
      obj.castShadow = true;
      //scene.add(obj);//
    });
  });

  const ambient = new THREE.AmbientLight(0x777777, 1.2); //   
  const spotLight = new THREE.SpotLight(0xffffff, 0); //
  spotLight.position.set(90, 90, 300); //
  //spotLight.target=(0,0,0);
  spotLight.angle = 3.14 / 6;
  const sunLight = new THREE.DirectionalLight(0xaeaeae, 1.03); //
  sunLight.castShadow = true;

  sunLight.position.set(100, 900, -300);

  sunLight.shadow.camera.near = 0.5;
  sunLight.shadow.camera.far = 1000;
  sunLight.shadow.camera.left = -2000;
  sunLight.shadow.camera.right = 200;
  sunLight.shadow.camera.top = 500;
  sunLight.shadow.camera.bottom = -1300;
  sunLight.shadow.mapSize = new THREE.Vector2(4096, 4096);
  const helper = new THREE.CameraHelper(sunLight.shadow.camera);

  const axisHelper = new THREE.AxesHelper(600); // 
  scene.add(ambient, axisHelper, spotLight, sunLight, helper);

  /* //
  const geometry = new THREE.IcosahedronGeometry(50);   //Geometry
  const material = new THREE.MeshPhongMaterial	({      //Material
    color: 0x999999,
    //opacity:0.5,//
    //transparent:true,
    specular:0x4488ee, //
    shininess:12      //
  }); 
  const material1 =new THREE.MeshPhongMaterial({color:0x000000});
  let cube = new THREE.Mesh(geometry, material); //Mesh
  let cube2 = new THREE.Mesh(geometry,material);
  let lCube = new THREE.Line(geometry, material1);
  cube.translateY(50);
  cube2.translateY(130);
  lCube.translateY(50);
  scene.add(cube,lCube,cube2); //*/

  //
  const floorGeometry = new THREE.PlaneGeometry(3000, 3000); //
  const floorMaterial = new THREE.MeshPhongMaterial({ color: 0x9aa0a4 }); //
  const floor = new THREE.Mesh(floorGeometry, floorMaterial);
  floor.receiveShadow = true;
  floor.rotation.x = -0.5 * Math.PI;
  floor.position.y = -1;
  scene.add(floor);

  const camera = new THREE.PerspectiveCamera( //
    45, //fov
    1, //
    1, //near,0.1
    3000
  ); //far,1000
  camera.position.set(2000, 0, 0); //
  camera.lookAt(0, 0, 0); //scene

  // stats,

  //------------------------------------

  //canvas
  function resizeRendererToDisplaySize(renderer) {
    let canvas = renderer.domElement;
    let width = window.innerWidth - 130;
    let height = window.innerHeight;
    let canvasPixelWidth = canvas.width / window.devicePixelRatio;
    let canvasPixelHeight = canvas.height / window.devicePixelRatio;
    let needResize = canvasPixelWidth !== width || canvasPixelHeight !== height;
    if (needResize) {
      renderer.setSize(width, height, false);
    }
    return needResize;
  }

  /*function click(e){
    //  raycaster  mouse 
      var raycaster = new THREE.Raycaster();
      var mouse = new THREE.Vector2();
  
      // , raycaster ,, -1  1
      mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
      mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
  
      //()
      raycaster.setFromCamera(mouse, camera);
  
      // 
      var intersects = raycaster.intersectObjects(scene.children);
  
      var objs=[];
      for (var i = 0; i < intersects.length; i++) {
          var intersect = intersects[i];
          if (intersect.object instanceof THREE.Mesh) {
              var obj = intersect.object;
              //
              obj.userData.distance = intersect.distance;
              objs.push(obj);
          }
      }
  
      //
      objs = objs.sort(function (a, b) {
      return a.userData.distance - b.userData.distance;
      });
  
      //objs
      //todo:...
      //....
  }*/

  //--------------------------------------
  let canvas = document.querySelector(domEl); //canavs
  //canavswebGlRender
  const renderer = new THREE.WebGLRenderer({ canvas, antialias: true });
  renderer.shadowMap.enabled = true;
  renderer.shadowMap.type = THREE.PCFSoftShadowMap;

  const controls = new OrbitControls(camera, renderer.domElement); //
  controls.enableDamping = true; //
  //renderer.setSize(1277,838);

  console.log(scene);
  function animate() {
    //
    controls.update();

    renderer.render(scene, camera);
    if (resizeRendererToDisplaySize(renderer)) {
      //
      let canvas = renderer.domElement;
      camera.aspect = canvas.clientWidth / canvas.clientHeight;
      camera.updateProjectionMatrix();
    }
    requestAnimationFrame(animate);
  }
  animate();
}
